// Hook to fetch GitHub stars
const useGithubStars = (githubUrl: string | undefined) => {
  const [stars, setStars] = useState<number | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (!githubUrl) {
      setLoading(false)
      return
    }

    const match = githubUrl.match(/github\.com\/([^/]+)\/([^/]+)/)
    if (!match) {
      setLoading(false)
      return
    }

    const [, owner, repo] = match
    const cacheKey = `github-stars-${owner}-${repo}`
    const cacheTimeKey = `${cacheKey}-time`
    const cacheDuration = 1000 * 60 * 60 // 1 hour

    // Check cache
    const cachedStars = localStorage.getItem(cacheKey)
    const cachedTime = localStorage.getItem(cacheTimeKey)
    if (cachedStars && cachedTime && Date.now() - parseInt(cachedTime) < cacheDuration) {
      setStars(parseInt(cachedStars))
      setLoading(false)
      return
    }

    fetch(`https://api.github.com/repos/${owner}/${repo}`)
      .then((res) => {
        if (!res.ok) throw new Error("Failed to fetch")
        return res.json()
      })
      .then((data) => {
        const starCount = data.stargazers_count
        setStars(starCount)
        localStorage.setItem(cacheKey, String(starCount))
        localStorage.setItem(cacheTimeKey, String(Date.now()))
      })
      .catch(() => {
        setStars(null)
      })
      .finally(() => {
        setLoading(false)
      })
  }, [githubUrl])

  return { stars, loading }
}

