// Image Carousel Component
interface ImageCarouselProps {
  images: WorkImage[]
  aspectRatio?: "video" | "square" | "4/3"
  className?: string
}

const ImageCarousel: React.FC<ImageCarouselProps> = ({
  images,
  aspectRatio = "video",
  className = "",
}) => {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [isHovered, setIsHovered] = useState(false)

  const nextImage = useCallback(() => {
    setCurrentIndex((prev) => (prev + 1) % images.length)
  }, [images.length])

  const prevImage = useCallback(() => {
    setCurrentIndex((prev) => (prev - 1 + images.length) % images.length)
  }, [images.length])

  // Auto-advance if multiple images and not hovered
  useEffect(() => {
    if (images.length <= 1 || isHovered) return
    const timer = setInterval(nextImage, 5000)
    return () => clearInterval(timer)
  }, [images.length, isHovered, nextImage])

  const aspectClass = {
    video: "aspect-video",
    square: "aspect-square",
    "4/3": "aspect-[4/3]",
  }

  return (
    <div
      className={`relative overflow-hidden ${className}`}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Images with gradient background */}
      <div className={`relative w-full ${aspectClass[aspectRatio]} bg-gradient-to-br from-[var(--bg-secondary)] via-[var(--bg-tertiary)] to-[var(--bg-secondary)]`}>
        {/* Decorative background pattern */}
        <div className="absolute inset-0 opacity-30">
          <div className="absolute inset-0" style={{
            backgroundImage: `radial-gradient(circle at 20% 80%, var(--accent-subtle) 0%, transparent 50%),
                              radial-gradient(circle at 80% 20%, var(--accent-subtle) 0%, transparent 50%)`
          }} />
        </div>
        {images.map((image, index) => (
          <div
            key={index}
            className={`absolute inset-0 flex items-center justify-center p-4 transition-all duration-500 ease-out ${
              index === currentIndex
                ? "opacity-100 translate-x-0"
                : index < currentIndex
                  ? "opacity-0 -translate-x-full"
                  : "opacity-0 translate-x-full"
            }`}
          >
            <img
              src={image.src}
              alt={image.alt || `Screenshot ${index + 1}`}
              className="max-w-full max-h-full object-contain rounded-lg shadow-lg"
            />
          </div>
        ))}
      </div>

      {/* Navigation Arrows - only show if multiple images */}
      {images.length > 1 && (
        <>
          <button
            onClick={(e) => {
              e.preventDefault()
              e.stopPropagation()
              prevImage()
            }}
            className="absolute left-2 top-1/2 -translate-y-1/2 p-2 rounded-full bg-white/90 dark:bg-black/50 backdrop-blur-sm shadow-lg text-slate-700 dark:text-slate-200 hover:bg-white dark:hover:bg-black/70 hover:scale-110 transition-all duration-200 z-10 opacity-0 group-hover:opacity-100"
            aria-label="上一张"
          >
            <CaretLeft className="w-5 h-5" />
          </button>
          <button
            onClick={(e) => {
              e.preventDefault()
              e.stopPropagation()
              nextImage()
            }}
            className="absolute right-2 top-1/2 -translate-y-1/2 p-2 rounded-full bg-white/90 dark:bg-black/50 backdrop-blur-sm shadow-lg text-slate-700 dark:text-slate-200 hover:bg-white dark:hover:bg-black/70 hover:scale-110 transition-all duration-200 z-10 opacity-0 group-hover:opacity-100"
            aria-label="下一张"
          >
            <CaretRight className="w-5 h-5" />
          </button>

          {/* Dots Indicator */}
          <div className="absolute bottom-3 left-1/2 -translate-x-1/2 flex gap-1.5 z-10">
            {images.map((_, index) => (
              <button
                key={index}
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  setCurrentIndex(index)
                }}
                className={`w-2 h-2 rounded-full transition-all duration-200 ${
                  index === currentIndex
                    ? "bg-white w-4"
                    : "bg-white/60 hover:bg-white/80"
                }`}
                aria-label={`跳转到第 ${index + 1} 张`}
              />
            ))}
          </div>

          {/* Image Counter */}
          <div className="absolute top-3 right-3 px-2 py-1 rounded-md bg-black/50 backdrop-blur-sm text-white text-xs z-10">
            {currentIndex + 1} / {images.length}
          </div>
        </>
      )}
    </div>
  )
}

