// Helper function to sort works (featured first)
const getSortedWorks = (): WorkItem[] => {
  return [...worksData].sort((a, b) => {
    if (a.featured === b.featured) return 0
    return a.featured ? -1 : 1
  })
}

const featuredWorks = getSortedWorks().filter((work) => work.featured).slice(0, 2)
const regularWorks = getSortedWorks().filter((work) => !work.featured)

// Get images for a work item (fallback to thumbnail if no images array)
const getWorkImages = (work: WorkItem): WorkImage[] => {
  if (work.images && work.images.length > 0) {
    return work.images
  }
  return [{ src: work.thumbnail, alt: work.title }]
}

